.catalog {
	&__nav {
		display: flex;
		margin-bottom: 42px;
		@include d {
			margin-bottom: 32px; }
		@include m {
			margin-bottom: 24px; } }
	&__nav &__link {
		position: relative;
		padding: 12px 24px;
		@include fp;
		font-size: 18px;
		line-height: (24/18);
		font-weight: 500;
		color: $text;
		transition: color .25s;
		@include m {
			padding: 12px 16px;
			font-size: 14px; }
		&:before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			width: 0;
			height: 4px;
			border-radius: 2px;
			background: $blue;
			transition: width .25s; }
		&:hover {
			color: $gray-dark; }
		&.active {
			color: $text;
			&:before {
				width: 100%; } }
		&:first-child {
			padding-left: 0; } }
	&__tags {
		display: flex;
		flex-wrap: wrap;
		margin: -8px 0 0 -8px;
		@include m {
			flex-wrap: nowrap;
			margin: 0 -24px;
			overflow: auto;
			overflow-x: auto;
			-ms-overflow-style: none;
			scrollbar-width: none;
			-webkit-overflow-scrolling: touch;
			&::-webkit-scrollbar {
			  display: none; }
			&:before,
			&:after {
				content: "";
				display: block;
				flex-shrink: 0;
				width: 24px;
				height: 1px; } } }
	& > &__tags {
		margin-bottom: 40px; }
	&__tag {
		min-width: 200px;
		margin: 8px 0 0 8px;
		border-radius: 8px;
		background: rgba($purple, .1);
		text-align: center;
		line-height: 56px;
		font-size: 13px;
		font-weight: 700;
		color: $blue;
		transition: opacity .25s;
		@include m {
			flex-shrink: 0;
			min-width: auto;
			margin: 0;
			padding: 0 35px; }
		&:first-child {
			background: $purple;
			color: $wh; }
		@include nl {
			@include m {
				margin-right: 8px; } }
		&:hover {
			opacity: .8; } }
	&__row {
		display: flex;
		@include d {
			display: block; } }
	&__container {
		flex-grow: 1; }
	&__sidebar {
		display: none;
		flex-shrink: 0;
		width: 240px;
		margin-left: 32px;
		@include m {
			display: block;
			width: 100%;
			margin: 32px 0 0; } }
	&__title {
		margin-bottom: 32px;
		@include m {
			margin-bottom: 24px; } }
	&__sorting {
		display: flex;
		align-items: center;
		margin-bottom: 32px;
		@include m {
			display: block;
			margin-bottom: 24px; }
		.checkbox {
			margin-right: 20px;
			@include m {
				display: none; } }
		.field {
			min-width: 200px;
			@include m {
				min-width: 100%; }
			@include nl {
				@include m {
					margin-bottom: 16px; } } } }
	&__box {
		display: flex;
		align-items: center;
		&:nth-child(2) {
			@include m {
				display: none; } }
		@include nl {
			margin-right: auto; } }
	&__actions {
		display: flex;
		align-items: center;
		@include d {
			display: none; }
		@include nl {
			margin-right: 24px;
			@include w {
				margin-right: 16px; } } }
	&__action {
		display: flex;
		align-items: center;
		height: 56px;
		padding: 0 20px;
		border-radius: 16px;
		font-weight: 600;
		color: $gray-dark;
		transition: background .25s;
		.icon {
			margin-right: 10px;
			font-size: 14px;
			fill: $text; }
		&:hover {
			background: rgba($border, .25); } }
	&__view {
		display: flex;
		align-items: center; }
	&__view &__link {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		width: 56px;
		height: 56px;
		border-radius: 16px;
		font-size: 0;
		transition: background .25s;
		.icon {
			font-size: 16px;
			fill: $text;
			transition: fill .2s; }
		&:hover .icon {
			fill: $blue; }
		&.active {
			background: $blue;
			.icon {
				fill: $wh; } } }
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: -40px -16px 0;
		@include m {
			display: block;
			margin: 0; }
		.game {
			flex: 0 0 calc(25% - 32px);
			width: calc(25% - 32px);
			margin: 40px 16px 0;
			@include w {
				flex: 0 0 calc(33.333% - 32px);
				width: calc(33.333% - 32px); }
			@include d {
				flex: 0 0 calc(50% - 32px);
				width: calc(50% - 32px); }
			@include m {
				width: 100%;
				margin: 0; }
			&:nth-child(n+10) {
				@include w {
					display: none; }
				@include d {
					display: block; } }
			@include nl {
				@include m {
					margin-bottom: 40px; } } } }
	&__btns {
		margin-top: 40px;
		text-align: center; }
	.widget {
		@include nl {
			margin-bottom: 64px;
			@include m {
				margin-bottom: 32px; } } }
	&.view &__list {
		display: block;
		margin: 0;
		.game {
			width: 100%;
			margin: 24px 0 0;
			&:nth-child(n+10) {
				@include w {
					display: none; }
				@include d {
					display: flex; } } } }
	&.view &__sidebar {
		display: block;
		@include d {
			display: none; } }
	&.view &__actions {
		@include w {
			display: none; } }
	&_channel &__sorting {
		margin-bottom: 40px;
		@include m {
			margin-bottom: 24px; } }
	&_channel &__tags {
		margin-right: auto; }
	&_channel &__tag {
		min-width: auto;
		padding: 0 30px;
		border-radius: 16px;
		color: $link;
		@include d {
			padding: 0 25px; }
		&:first-child {
			color: $wh; } }
	&_channel &__btns {
		margin-top: 24px; }
	&_channel &__list .game {
		@include nl {
			@include m {
				margin-bottom: 24px; } } }
	&_channel .game__details {
		padding-bottom: 24px; } }

@include dark {
	.catalog {
		&__action {
			background: $dark1;
			color: $wh;
			.icon {
				fill: $wh; }
			&:hover {
				background: rgba($dark1, .7); }
			@include nl {
				margin-right: 8px; } }
		&__view .catalog__link {
			.icon {
				fill: $wh; }
			&:hover .icon {
				fill: $blue; }
			&.active .icon {
				fill: $wh; } }
		&__nav .catalog__link {
			color: $wh;
			&:hover {
				color: $gray-dark; }
			&.active {
				color: $wh; } } } }

